"use client"

import { useEffect } from "react"
import { useRouter } from "next/navigation"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { useToast } from "@/components/ui/use-toast"
import { useCart } from "@/contexts/cart-context"

export default function CartPage() {
  const router = useRouter()
  const { toast } = useToast()
  const { state, updateQuantity, removeItem, clearCart, total } = useCart()

  // Redirect to scan page if no table is selected
  useEffect(() => {
    if (!state.tableId) {
      toast({
        title: "No table selected",
        description: "Please scan your table's QR code first",
        variant: "destructive",
      })
      router.push("/scan")
    }
  }, [state.tableId, router, toast])

  const handleCheckout = async () => {
    if (state.items.length === 0) {
      toast({
        title: "Cart is empty",
        description: "Please add items to your cart before checking out",
        variant: "destructive",
      })
      return
    }

    try {
      const response = await fetch("/api/orders", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          tableId: state.tableId,
          items: state.items.map(item => ({
            menuItemId: item.id,
            quantity: item.quantity,
            price: item.price
          }))
        }),
      })

      if (!response.ok) {
        throw new Error("Failed to create order")
      }

      clearCart()
      toast({
        title: "Order placed successfully",
        description: "Your order has been sent to the kitchen",
      })
      router.push("/menu")
    } catch (error) {
      console.error("Error creating order:", error)
      toast({
        title: "Error",
        description: "Failed to place order. Please try again.",
        variant: "destructive",
      })
    }
  }

  if (!state.tableId) {
    return null // Prevent flash of content before redirect
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="text-3xl font-bold mb-8">Your Cart</h1>
      {state.items.length === 0 ? (
        <Card className="p-6 text-center">
          <p className="text-gray-500 mb-4">Your cart is empty</p>
          <Button onClick={() => router.push("/menu")}>View Menu</Button>
        </Card>
      ) : (
        <div className="space-y-6">
          <Card className="p-6">
            <div className="space-y-4">
              {state.items.map((item) => (
                <div
                  key={item.id}
                  className="flex items-center justify-between border-b last:border-0 pb-4 last:pb-0"
                >
                  <div>
                    <h3 className="font-medium">{item.name}</h3>
                    <p className="text-sm text-gray-600">
                      ${(item.price * item.quantity).toFixed(2)}
                    </p>
                  </div>
                  <div className="flex items-center space-x-2">
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => updateQuantity(item.id, item.quantity - 1)}
                    >
                      -
                    </Button>
                    <span className="w-8 text-center">{item.quantity}</span>
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => updateQuantity(item.id, item.quantity + 1)}
                    >
                      +
                    </Button>
                    <Button
                      variant="destructive"
                      size="sm"
                      onClick={() => removeItem(item.id)}
                    >
                      Remove
                    </Button>
                  </div>
                </div>
              ))}
            </div>
          </Card>

          <Card className="p-6">
            <div className="flex justify-between items-center mb-6">
              <span className="text-lg font-semibold">Total:</span>
              <span className="text-lg font-semibold">
                ${total.toFixed(2)}
              </span>
            </div>
            <div className="flex space-x-4">
              <Button
                variant="outline"
                className="flex-1"
                onClick={() => router.push("/menu")}
              >
                Add More Items
              </Button>
              <Button
                className="flex-1"
                onClick={handleCheckout}
              >
                Place Order
              </Button>
            </div>
          </Card>
        </div>
      )}
    </div>
  )
}
